import React, { useState, useLayoutEffect } from 'react';
import Button from '@material-ui/core/Button';
import classes from './ProfileConfig.module.css';
import Input from '../../components/Controls/Input';
import Select from '../../components/Controls/Select';
import { Grid } from '@material-ui/core';
import { Form, useForm } from '../../shared/useForm';
import SnackBar from '../../components/UI/SnackBar/SnackBar';
import Spinner from '../../components/UI/Spinner/Spinner';
import axios from 'axios';

const initialValues = {
  name: '',
  firstLastName: '',
  secondLastName: '',
  email: '',
  phoneNumber: '',
  plaza: '',
  gender: '',
  department: '',
};

const getDepartment = () => [
  {
    id: 'ELÉCTRICA, ELECTRÓNICA, BIOMÉDICA',
    title: 'ELÉCTRICA, ELECTRÓNICA, BIOMÉDICA',
  },
  { id: 'SISTEMAS E INFORMÁTICA', title: 'SISTEMAS E INFORMÁTICA' },
  { id: 'METAL MECÁNICA Y AERONÁUTICA', title: 'METAL MECÁNICA Y AERONÁUTICA' },
  { id: 'INDUSTRIAL', title: 'INDUSTRIAL' },
  { id: 'CIENCIAS BÁSICAS', title: 'CIENCIAS BÁSICAS' },
  { id: 'ECONÓMICO ADMINISTRATIVO', title: 'ECONÓMICO ADMINISTRATIVO' },
];

const getGender = () => [
  { id: 'F', title: 'F' },
  { id: 'M', title: 'M' },
];

const ProfileConfig = (props) => {
  const [snackStatus, setSnackStatus] = useState({
    openSnackBar: false,
    snackBarMessage: '',
    severity: '',
  });
  const [loading, setLoading] = useState(false);

  useLayoutEffect(() => {
    request();
    // eslint-disable-next-line
  }, []);

  const request = () => {
    setLoading((loading) => !loading);
    //let url ='/api/profile-config'
    axios
      .get('/api/profile-config')
      .then((response) => {
        setValues({
          name: response.data.name,
          firstLastName: response.data.firstLastName,
          secondLastName: response.data.secondLastName,
          email: response.data.email,
          phoneNumber: response.data.phoneNumber,
          plaza: response.data.plaza,
          gender: response.data.gender,
          department: response.data.department,
        });
        setLoading((loading) => !loading);
      })
      .catch((error) => {
        setLoading((loading) => !loading);
        if (error.response) {
          //Se realizo la peticion y el servidor respondio con un codigo de status
          setSnackStatus({
            openSnackBar: true,
            severity: 'error',
            snackBarMessage: error.response.data.message,
          });
        } else if (error.request) {
          //Se realizo la peticion pero no se recibio una respuesta
          window.alert('Error: ' + error.request.data.message);
        } else {
          //Algo ocurrio al preparar la peticion que desencadeno un error
          console.log('Error: ' + error.message);
        }
        console.log(error.config);
      });
  };

  const validate = (fieldValues = values) => {
    let temp = { ...errors };

    if ('name' in fieldValues)
      temp.name = fieldValues.name ? '' : 'Campo requerido';
    if ('firstLastName' in fieldValues)
      temp.firstLastName = fieldValues.firstLastName ? '' : 'Campo requerido';
    if ('secondLastName' in fieldValues)
      temp.secondLastName = fieldValues.secondLastName ? '' : 'Campo requerido';
    if ('plaza' in fieldValues)
      temp.plaza = fieldValues.plaza ? '' : 'Campo requerido';
    if ('phoneNumber' in values)
      temp.phoneNumber = values.phoneNumber
        ? /^(\d{10}|\d{11}|\d{12})$/.test(values.phoneNumber)
          ? (temp.phoneNumber = '')
          : (temp.phoneNumber = 'Número debe contener entre 10 y 12 dígitos')
        : (temp.phoneNumber = 'Campo obligatorio');
    if ('email' in fieldValues)
      temp.email = fieldValues.email
        ? /$^|.+@.+..+/.test(values.email)
          ? (temp.email = '')
          : (temp.email = 'Email no valido')
        : (temp.email = 'Campo obligatorio');
    if ('gender' in fieldValues)
      temp.gender = fieldValues.gender ? '' : 'Campo requerido';
    if ('department' in fieldValues)
      temp.department = fieldValues.department ? '' : 'Campo requerido';

    setErrors({
      ...temp,
    });

    if (fieldValues == values) return Object.values(temp).every((x) => x == '');
  };

  const { values, setValues, errors, setErrors, handleInputChange } = useForm(
    initialValues,
    true,
    validate
  );

  const handleSubmit = (e) => {
    e.preventDefault();
    if (validate()) {
      setLoading((loading) => !loading);
      let post = {
        name: values.name,
        firstLastName: values.firstLastName,
        secondLastName: values.secondLastName,
        email: values.email,
        phoneNumber: values.phoneNumber,
        plaza: values.plaza,
        gender: values.gender,
        department: values.department,
      };
      //let url ='/auth/profile-config'
      axios
        .post(
          '/auth/profile-config',
          post
        )
        .then((response) => {
          setLoading((loading) => !loading);
          setSnackStatus({
            openSnackBar: true,
            severity: 'success',
            snackBarMessage: 'Datos registrados correctamente',
          });
        })
        .catch((error) => {
          if (error.response) {
            //Se realizo la peticion y el servidor respondio con un codigo de status
            setSnackStatus({
              openSnackBar: true,
              severity: 'error',
              snackBarMessage: error.response.data.message,
            });
          } else if (error.request) {
            //Se realizo la peticion pero no se recibio una respuesta
            window.alert('Error: ' + error.request.data.message);
          } else {
            //Algo ocurrio al preparar la peticion que desencadeno un error
            console.log('Error: ' + error.message);
          }
          console.log(error.config);
        });
    }
  };

  const redirect = () => {
    props.history.push('/profile-config/change-password');
  };

  let form = (
    <Form className={classes.form} onSubmit={handleSubmit}>
      <h1>Datos del participante</h1>
      <p style={{ textDecoration: 'underline' }}>
        Es necesario llenar sus datos para inscribirse a un curso
      </p>
      <Grid container>
        <Grid item xs={6}>
          <Input
            name='name'
            placeholder='Nombre'
            label='Nombre'
            value={values.name || ''}
            onChange={handleInputChange}
            inputProps={{ maxLength: 50 }}
            error={errors.name}
          />

          <Input
            name='firstLastName'
            placeholder='Apellido paterno'
            label='Apellido materno'
            value={values.firstLastName || ''}
            onChange={handleInputChange}
            inputProps={{ maxLength: 20 }}
            error={errors.firstLastName}
          />

          <Input
            name='secondLastName'
            placeholder='Apellido materno'
            label='Apellido materno'
            value={values.secondLastName || ''}
            onChange={handleInputChange}
            inputProps={{ maxLength: 20 }}
            error={errors.secondLastName}
          />

          <Input
            name='email'
            placeholder='Email'
            label='Email'
            value={
              values.email
                ? values.email.replace(/\s/g, '')
                : values.email || ''
            }
            onChange={handleInputChange}
            inputProps={{ maxLength: 100 }}
            error={errors.email}
          />
        </Grid>
        <Grid item xs={6}>
          <Input
            name='phoneNumber'
            placeholder='Telefono'
            label='Telefono'
            type='number'
            value={values.phoneNumber || ''}
            onChange={handleInputChange}
            error={errors.phoneNumber}
            inputProps={{
              onWheel: (event) => event.currentTarget.blur(),
              min: 0,
            }}
          />
          <Input
            name='plaza'
            placeholder='Plaza'
            label='Plaza'
            value={values.plaza || ''}
            onChange={handleInputChange}
            inputProps={{ maxLength: 15 }}
            error={errors.plaza}
          />
          <Select
            name='gender'
            label='Genero'
            options={getGender()}
            value={values.gender || ''}
            onChange={handleInputChange}
            error={errors.gender}
          />
          <Select
            name='department'
            label='Departamento'
            options={getDepartment()}
            value={values.department || ''}
            onChange={handleInputChange}
            error={errors.department}
          />
        </Grid>
      </Grid>
      <Grid>
        <Button
          fullWidth
          variant='contained'
          color='primary'
          className={classes.submit}
          style={{ marginTop: '15px' }}
          onClick={redirect}
        >
          Cambiar contraseña
        </Button>

        <Button
          type='submit'
          fullWidth
          variant='contained'
          color='primary'
          className={classes.submit}
          style={{ marginTop: '15px' }}
        >
          Guardar
        </Button>
      </Grid>
    </Form>
  );

  if (loading) {
    form = <Spinner />;
  }

  return (
    <div className={classes.paper}>
      {form}
      <SnackBar
        openSnackBar={snackStatus.openSnackBar}
        closeSnackBar={() => setSnackStatus({ openSnackBar: false })}
        snackBarMessage={snackStatus.snackBarMessage}
        severity={snackStatus.severity}
      />
    </div>
  );
};

export default ProfileConfig;
