import React from 'react';
import { TextField } from '@material-ui/core';

const styles = {
  width: '85%',
  margin: '15px',
};

export default function Input(props) {
  const {
    name,
    label,
    placeholder,
    value,
    error = null,
    onChange,
    type,
    disabled,
    inputProps,
    InputLabelProps,
    className,
  } = props;
  return (
    <TextField
      className={className}
      disabled={disabled}
      style={styles}
      variant='outlined'
      label={label}
      placeholder={placeholder}
      name={name}
      value={value}
      onChange={onChange}
      type={type}
      inputProps={inputProps}
      InputLabelProps={InputLabelProps}
      {...(error && { error: true, helperText: error })}
    />
  );
}
