import React from 'react';
import {
  FormControl,
  InputLabel,
  Select as MuiSelect,
  MenuItem,
  FormHelperText,
} from '@material-ui/core';

const styles = {
  margin: '15px',
  padding: '0',
  width: '85%',
};

export default function Select(props) {
  const {
    name,
    label,
    value,
    error = null,
    onChange,
    options,
    disabled,
  } = props;

  return (
    <FormControl
      variant='outlined'
      style={styles}
      {...(error && { error: true })}
    >
      <InputLabel>{label}</InputLabel>
      <MuiSelect
        disabled={disabled}
        label={label}
        name={name}
        value={value}
        onChange={onChange}
      >
        <MenuItem value=''>NINGUNO</MenuItem>
        {options.map((item) => (
          <MenuItem key={item.id} value={item.id}>
            {item.title}
          </MenuItem>
        ))}
      </MuiSelect>
      {error && <FormHelperText>{error}</FormHelperText>}
    </FormControl>
  );
}
